import { useState, useEffect } from "react";
import {
	Container,
	Body,
	Footer,
	Steps,
	Button,
	ButtonsGroup,
	Text,
} from "../Form";
import BarLoader from "react-spinners/BarLoader";
import axios from "../lib/axios";
import queryString from "query-string";

import "./Home.css";

const params = !!window.location.search
	? queryString.parse(window.location.search)
	: {};

export default ({ next, onSubmit }) => {
	const [loading, setLoading] = useState(true);
	const [id, setId] = useState("");
	const [error, setError] = useState();

	useEffect(() => {
		if (params?.skipIdCheck !== undefined) {
			setLoading(false);
			onSubmit(params.id);
			next();
			return;
		}

		if (!params?.id) {
			setError("Неизвестная акция");
			setLoading(false);
			return;
		}

		const id = axios({
			method: "post",
			url: "/CheckID",
			data: { id: params.id },
		})
			.then(async (response) => {
				const { data } = response;
				if (data.error) data.text && setError(data.text);
				else {
					onSubmit(params.id);
					next();
				}
				return response;
			})
			.catch((e) => {
				switch (e.response?.status) {
					case 400:
						setError(
							"Некорректные данные в запросе\n" +
								e.response.data.error
						);
						break;
					case 404:
						setError(e.response.data);
						break;
					case 500:
						setError("Произошла ошибка на стороне сервера");
						break;
					case 503:
						setError("Сервис временно недоступен");
						break;
					default:
						setError(`Неизвестная ошибка\n` + e);
				}
			})
			.finally(() => setLoading(false));
	}, []);

	if (loading)
		return (
			<Container>
				<Body center>
					<BarLoader color="#323b4c" />
					<Text center>Пожалуйста, подождите...</Text>
				</Body>
			</Container>
		);

	if (error)
		return (
			<Container>
				<Body center>
					<Text center>{error}</Text>
				</Body>
			</Container>
		);

	return (
		<Container>
			<Body center>
				<div className="home-hr" />
				<div className="home-title">Получить купон с ИМ ЗО</div>
			</Body>
			<Footer>
				<Button primary iconLeft="check" onPress={next}>
					Начать
				</Button>
			</Footer>
		</Container>
	);
};
