import { useState, useCallback } from "react";

import axios from "../lib/axios";

import {
	Container,
	Body,
	Footer,
	Steps,
	Button,
	ButtonsGroup,
	Title,
	InputAutocomplete,
	Error,
} from "../Form";

export default ({ prev, next, defaultValue, onSubmit }) => {
	const [error, setError] = useState();
	const [loading, setLoading] = useState(false);
	const [value, setValue] = useState(defaultValue);
	const [suggestions, setSuggestions] = useState([]);

	const onSuggestionsClearRequested = useCallback(
		() => setSuggestions([]),
		[]
	);

	const onSuggestionsFetchRequested = useCallback(({ value }) => {
		axios({
			method: "post",
			url: "/SuggestName",
			data: { name: value },
		}).then(({ data }) => setSuggestions(data));
	}, []);

	const inputProps = {
		className: "input",
		placeholder: "Иван",
		value,
		onChange: (e, { newValue }) => setValue(newValue),
		autoFocus: true,
	};

	const submit = useCallback(() => {
		setLoading(true);
		setError();
		axios({
			method: "post",
			url: "/CheckName",
			data: { name: value },
		})
			.then(async ({ data }) => {
				if (data.error) data.text && setError(data.text);
				else {
					onSubmit(value);
					next();
				}
			})
			.catch((e) => {
				switch (e.response?.status) {
					case 400:
						setValue("");
						setError(e.response.data.error);
						break;
					case 500:
						setError(
							"Произошла ошибка на стороне сервера\n" +
								e.response.data
						);
						break;
					default:
						setError(`Неизвестная ошибка\n` + e);
				}
			})
			.finally(() => setLoading(false));
	}, [value]);

	return (
		<Container>
			<Body>
				<Title>Введите имя</Title>
				<InputAutocomplete
					suggestions={suggestions}
					onSuggestionsFetchRequested={onSuggestionsFetchRequested}
					onSuggestionsClearRequested={onSuggestionsClearRequested}
					getSuggestionValue={(item) => item}
					renderSuggestion={(item) => {
						return <span>{item}</span>;
					}}
					inputProps={inputProps}
				/>
				{error && <Error>{error}</Error>}
			</Body>
			<Footer loading={loading}>
				<Steps step={1} total={3} />
				<ButtonsGroup>
					<Button
						secondary
						disabled
						icon="arrow-left"
						onPress={prev}
					/>
					<Button
						disabled={loading || !value.trim().length}
						iconRight="arrow-right"
						onPress={submit}
					>
						Далее
					</Button>
				</ButtonsGroup>
			</Footer>
		</Container>
	);
};
