import Mask from "react-input-mask";
import Autosuggest from "react-autosuggest";
import "./index.css";

export const InputMask = (props) => {
  return <Mask className="input" {...props} autoFocus inputMode="numeric" />;
};

export const InputAutocomplete = (props) => {
  return <Autosuggest {...props} autoFocus />;
};

export default (props) => {
  return <input className="input" {...props} autoFocus />;
};
