import { useRef, useState, useCallback, useEffect } from "react";
import axios from "../lib/axios";

import {
	Container,
	Body,
	Footer,
	Steps,
	Button,
	ButtonsGroup,
	Title,
	Input,
	Error,
} from "../Form";

export default ({ prev, next, name, phone, id, onSubmit }) => {
	const [error, setError] = useState();
	const [loading, setLoading] = useState(false);
	const [code, setCode] = useState("");

	const onChange = useCallback((e) => {
		setCode(e.target.value);
	}, []);

	const submit = useCallback(() => {
		setLoading(true);
		setError();
		axios({
			method: "post",
			url: "/GetKupon",
			data: { name, phone, code, id },
		})
			.then(async ({ data }) => {
				console.log(data);
				if (data.error) data.text && setError(data.text);
				else {
					onSubmit(data.text);
					next();
				}
			})
			.catch((e) => {
				setCode("");
				switch (e.response?.status) {
					case 400:
						setError(e.response.data.error);
						break;
					case 429:
						setError("Слишком много запросов в секунду");
						break;
					case 500:
						setError("Произошла ошибка на стороне сервера");
						break;
					case 503:
						setError("Сервис временно недоступен");
						break;
					default:
						setError(`Неизвестная ошибка\n` + e);
				}
			})
			.finally(() => setLoading(false));
	}, [name, phone, code, id]);

	useEffect(() => {
		code.length === 4 && submit();
	}, [code]);

	return (
		<Container>
			<Body>
				<Title>Введите код из смс</Title>
				<Input
					type="number"
					placeholder="1234"
					maxLength="4"
					value={code}
					onChange={onChange}
					disabled={loading}
				/>
				{error && <Error>{error}</Error>}
			</Body>
			<Footer loading={loading}>
				<Steps step={3} total={3} />
				<ButtonsGroup>
					<Button
						disabled={loading}
						secondary
						icon="arrow-left"
						onPress={prev}
					/>
					<Button
						disabled={loading || !code.trim().length}
						iconLeft="check"
						onPress={submit}
					>
						Последний шаг
					</Button>
				</ButtonsGroup>
			</Footer>
		</Container>
	);
};
