import classNames from "classnames";
import "./index.css";

import ArrowLeftIcon from "./icons/arrow-left";
import ArrowRightIcon from "./icons/arrow-right";
import CheckmarkIcon from "./icons/checkmark";

const icons = {
  "arrow-left": ArrowLeftIcon,
  "arrow-right": ArrowRightIcon,
  check: CheckmarkIcon,
};

export const ButtonsGroup = ({ children }) => {
  return <div className="buttons-group">{children}</div>;
};

export default ({
  children,
  onPress,
  primary,
  secondary,
  disabled,
  icon,
  iconLeft,
  iconRight,
}) => {
  const Icon = icons[icon];
  const IconLeft = icons[iconLeft];
  const IconRight = icons[iconRight];
  return (
    <div
      className={classNames("button", {
        primary,
        secondary,
        disabled,
        "no-padding": !children,
      })}
      onClick={disabled ? null : onPress}
    >
      {IconLeft && (
        <div className="button-icon button-icon-left">
          <IconLeft />
        </div>
      )}
      {Icon && (
        <div className="button-icon">
          <Icon />
        </div>
      )}
      {children && <div className="button-text">{children}</div>}
      {IconRight && (
        <div className="button-icon button-icon-right">
          <IconRight />
        </div>
      )}
    </div>
  );
};
