import { useState, useCallback } from "react";
import axios from "../lib/axios";

import {
	Container,
	Body,
	Footer,
	Steps,
	Button,
	ButtonsGroup,
	Title,
	InputMask,
	Error,
} from "../Form";

export default ({ prev, next, defaultValue, onSubmit }) => {
	const [error, setError] = useState();
	const [loading, setLoading] = useState(false);
	const [value, setValue] = useState(defaultValue);

	const onChange = useCallback((e) => {
		setValue(e.target.value);
	}, []);

	const sendCode = useCallback(() => {
		return axios({
			method: "post",
			url: "/SendVerCode",
			data: { phone: value },
		})
			.then(({ data }) => {
				data.text && setError(data.text);
				if (!data) {
					onSubmit(value);
					next();
				}
			})
			.catch((e) => {
				switch (e.response?.status) {
					case 400:
						setError(
							"В запросе переданы некорректные данные\n" +
								e.response.data.error
						);
						break;
					case 429:
						setError("Слишком много запросов в секунду");
						break;
					case 500:
						setError("Произошла ошибка на стороне сервера");
						break;
					case 503:
						setError("Сервис временно недоступен");
						break;
					default:
						setError(`Неизвестная ошибка\n` + e);
				}
			});
	}, [value]);

	const submit = useCallback(() => {
		setLoading(true);
		setError();
		axios({
			method: "post",
			url: "/CheckPhone",
			data: { phone: value },
		})
			.then(async ({ data }) => {
				data.text && setError(data.text);
				if (data.correct) await sendCode();
			})
			.catch((e) => {
				switch (e.response?.status) {
					case 400:
						setError(e.response.data.error);
						break;
					case 429:
						setError("Слишком много запросов в секунду");
						break;
					case 500:
						setError("Произошла ошибка на стороне сервера");
						break;
					case 503:
						setError("Сервис временно недоступен");
						break;
					default:
						setError(`Неизвестная ошибка\n` + e);
				}
			})
			.finally(() => setLoading(false));
	}, [value]);

	return (
		<Container>
			<Body>
				<Title>Введите номер телефона</Title>
				<InputMask
					disabled={loading}
					mask="+7 (999) 999-99-99"
					placeholder="+7 (999) 999-99-99"
					value={value}
					onChange={onChange}
				/>
				{error && <Error>{error}</Error>}
			</Body>
			<Footer loading={loading}>
				<Steps step={2} total={3} />
				<ButtonsGroup>
					<Button
						disabled={loading}
						secondary
						icon="arrow-left"
						onPress={prev}
					/>
					<Button
						disabled={loading || !value.length || value.match(/_/)}
						iconRight="arrow-right"
						onPress={submit}
					>
						Далее
					</Button>
				</ButtonsGroup>
			</Footer>
		</Container>
	);
};
