import classNames from "classnames";
import "./index.css";

export default ({ step, total }) => {
  return (
    <div className="steps">
      <div className="steps-text">
        Шаг {step} из {total}
      </div>
      <div className="steps-nav">
        {[...Array(total)].map((e, i) => (
          <div
            key={i}
            className={classNames("steps-nav-item", { passed: i < step })}
          />
        ))}
      </div>
    </div>
  );
};
